nav{
display: flex;
align-items: center;
justify-content: space-between;
padding-top: 10px;
padding-left: 8%;
padding-right: 8%;
background-color: rgb(255, 255, 255);
}

.ptitre{
color:rgb(0, 0, 0);
cursor:pointer;
font-size: 35px;
letter-spacing: 1px;
}

nav ul li{
list-style-type: none;
display: inline-block;
padding: 10px 25px;

}

nav ul li a{
color:rgb(0, 0, 0);
text-decoration: none;
font-weight: bold;
text-transform: capitalize;
}

nav ul li a:hover{
cursor: pointer;
transition: .4s;
color:#0085f9;
}

.btn{
background-color: #000000;
color: rgb(255, 255, 255);
text-decoration: none;
border: 2px solid transparent;
padding: 10px 25px;
font-weight: bold;
border-radius: 10px;
transition: transform .4s;
}

.btn:hover{
cursor: pointer;
transform: scale(1.2);
}

/* --------------- FIN MENU --------------- */

.content-left{
float: left;
}

.content-right{
float: right;
}


